@import '../../styles/tools';
@import '../../styles/theme';

.MarkersToolbar {
  width: 100%;
  height: 40px;
  @include flexContainer(row, nowrap, flex-end, center);
  @include PaddingN(all, 2);
  @include MarginN(top bottom, 2);
  border: 1px solid $default25;
  border-radius: 4px;
  box-shadow: 0 1px 3px $default50;

  & > .IconButton {
    width: 32px;
    height: 24px;
    @include flexContainer(column, nowrap, center, center);
    transform-origin: center;

    & > svg {
      &:not(.Icon-disabled) {
        cursor: pointer;
        &:hover {
          transform: scale(1.1);
          & > path {
            stroke: $primary75;
          }
        }

        &:active {
          transform: scale(0.9);
          & > path {
            stroke: $primary100;
          }
        }
      }

    }
  }
}