@import '../../styles/tools';
@import '../../styles/theme';

.MarkersList {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;

  & > .MarkersList--modal {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: $mattGlassWhite;
  }

  & > .MarkersList--list {
    width: 100%;
    list-style: none;

    & > .MarkersList--list-item {
      width: 100%;
      @include PaddingN(all, 2);
      @include MarginN(bottom, 1);
      @include flexContainer(column, nowrap, flex-start, flex-start);
      cursor: pointer;
      border: 1px solid $default25;
      border-radius: 4px;
      position: relative;

      &:last-of-type {
        @include MarginN(bottom, 0)
      }

      &:hover {
        border-color: $primary50;
        box-shadow: 1px 1px 3px $primary25;


        & .CloseIcon,
        & .EditIcon {
          visibility: visible;
        }
      }

      &.list-item--selected {
        background-color: $primary25;
      }

      & > .MarkersList--item-name {
        flex: 1 1 auto;
        font-size: 1rem;
        font-weight: bold;
        color: $default100;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        user-select: none;
        @include PaddingN(bottom, 1);
        @include PaddingN(right, 2)
      }

      & > .MarkersList--icon-edit {
        position: absolute;
        top: 0;
        right: 1.5rem;
        width: 1.5rem;
        height: 1.5rem;
        @include flexContainer(column, nowrap, center, center);
        color: $primary50;

        &:hover {
          color: $primary100;
        }
      }

      & > .MarkersList--icon-delete {
        position: absolute;
        top: 0;
        right: 0;
        width: 1.5rem;
        height: 1.5rem;
        @include flexContainer(column, nowrap, center, center);
        color: $secondary50;

        &:hover {
          color: $secondary100;
        }
      }

      & > .MarkersList--item-details {
        width: 100%;
        flex: 1 1 auto;
        color: $default75;
        font-size: 0.75rem;

        & > .MarkersList--details-section {
          width: 100%;
          @include flexContainer(row, nowrap, space-between, flex-start);

          & > .MarkersList--info-box {
            flex: 1 1 auto;
            max-width: calc(50% - 2px);
            @include flexContainer(row, nowrap, flex-start, center);

            & > .MarkersList--label-name {
              width: 2.5em;
              font-weight: bold;
              user-select: none;
            }

            & > .MarkersList--label-content {
              width: calc(100% - 2em);
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
        }

        & > .MarkersList--details-description-section {
          width: 100%;
          display: block;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;

          & > .MarkersList--description-name {
            font-weight: bold;
          }
        }
      }

      & > .MarkersList--label-link {
        font-size: 0.75rem;
        color: $primary50;
        cursor: pointer;
        @include PaddingN(top, 1);
      }

      & .EditIcon {
        visibility: hidden;

        &:hover {
          & path {
            stroke: $primary100;
            transform-origin: center;
            transform: scale(1.1);
          }
        }
      }

      & .CloseIcon {
        visibility: hidden;

        &:hover {
          & > path {
            stroke: $secondary100;
            transform-origin: center;
            transform: scale(1.1);
          }
        }
      }
    }
  }
}