@import '../../styles/const_style';
@import '../../styles/tools';
@import '../../styles/theme';

.MarkerDataForm {
  width: 100%;
  height: $markerDataFormHeight;
  position: relative;
  @include MarginN(bottom, 4);
  @include flexContainer(column, nowrap, space-between, flex-start);

  &.MarkerDataForm--hidden {
    display: none;
  }

  & > .MarkerDataForm--section {
    flex: 1 1 auto;
    width: 100%;

    &.form-title {
      @include flexContainer(row, nowrap, space-between, flex-start);

      & > .section--title-content {
        flex: 1 1 auto;
        width: calc(100% - 3rem);
      }
      & > .icon-close--container {
        width: 3rem;
        flex: 1 1 auto;
        @include flexContainer(column, nowrap, center, flex-end);
        transform-origin: center;

        & .CloseIcon {
          &:hover {
            transform: scale(1.1);
            & > path {
              stroke: $primary100;
            }
          }

          &:active {
            transform: scale(0.9);
            & > path {
              stroke: $primary100;
            }
          }
        }
      }
    }

    &.form-content {
      width: 100%;
      @include flexContainer(column, nowrap, flex-start, flex-start);
      font-size: 0.75rem;

      & > .MarkerDataForm--form-control {
        width: 100%;
        @include flexContainer(row, nowrap, space-between, center);
        flex: 1 1 auto;
        @include MarginN(bottom, 3);

        &:last-of-type {
          @include MarginN(bottom, 0);
        }

        & label {
          display: block;
          width: 100px;
          font-weight: bold;
          user-select: none;
        }

        & input {
          display: block;
          width: calc(100% - 100px);
          border: none;
          outline: none;
          border-bottom: 1px solid $default25;

          &:hover {
            border-color: $default50;
          }

          &:focus {
            border-color: $primary75;
          }
        }

        &.description {
          flex-direction: column;
          align-items: flex-start;

          & > #description {
            max-width: 100%;
            width: 100% !important;
            display: block;
            resize: vertical;
            max-height: 50px;
            height: 50px !important;
          }
        }

      }
    }

    &.form-actions {
      @include MarginN(top, 4);
      width: 100%;
      @include flexContainer(row, nowrap, flex-end, center);

      & > button {
        @include MarginN(right, 1);

        &:last-of-type {
          @include MarginN(right, 0);
        }

        &.MarkerDataForm--btn-ok {
          @include Button(medium, 4px, btnOutlined, primary);
        }
        &.MarkerDataForm--btn-clear {
          @include Button(medium, 4px, btnOutlined, default);
        }

      }
    }
  }
}