@import '../../styles/tools';

.MapContainer {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;

  & > .map--btn-zoom {
    @include Button(large, 4px, btnOutlined, default);
    width: 32px;
    // height: 24px;
    font-weight: bold;
    font-size: 1rem;
    position: absolute;
    left: 16px;
    outline: none;


    &.map--btn-zoom-in {
      top: 16px;
    }

    &.map--btn-zoom-out {
      top: 52px;
    }
  }
}