@import './theme';

//sizes
// $size0: 0;
// $size1: 0.25rem;
// $size2: 0.5rem;
// $size3: 0.75rem;
// $size4: 1rem;

@function size($value) {
  @return #{ $value * 0.25 }rem;
}

@mixin set($name: padding, $sides: all, $size: 0) {
  @if $sides == all {
    #{ $name }: $size;
  } @else {
    @each $side in $sides {
      #{ $name }-#{ $side }: $size;
    }
  }
}

// padding
@mixin Padding($sides: all, $size: 0) {
  @include set(padding, $sides, $size);
}

@mixin PaddingN($sides: all, $grade: 0) {
  @include Padding($sides, size($grade));
}

// margin
@mixin Margin($sides: all, $size: 0) {
  @include set(margin, $sides, $size);
}

@mixin MarginN($sides: all, $grade: 0) {
  @include Margin($sides, size($grade));
}

@mixin flexContainer($direction: row, $wrapping: wrap, $justifyContent: flex-start, $alignItems: center) {
  display: flex;
  flex-flow: $direction $wrapping;
  justify-content: $justifyContent;
  align-items: $alignItems;
}

// button
@mixin Button($size: medium, $cornerRadius: 0, $variant: btnOutlined, $type: default) {
  @if $size == small {
    height: $heightSmall;
  } @else if $size == large {
    height: $heightLarge;
  } @else {
    height: $heightMedium;
  }
  width: 75px;
  border-radius: $cornerRadius;
  outline: none;
  text-align: center;

  @if $variant == btnOutlined {
    background-color: $transparent;
    border: 1px solid $default25;
    color: $default50;

    &:not(.btn-disabled) {
      cursor: pointer;
      &:hover {
        background-color: $default25;
        color: $default75;
      }

      &:active {
        background-color: $default50;
        border-color: $default50;
        color: $default100;
      }
    }

    @if $type == primary {
      border-color: $primary25;
      color: $primary50;
      &:hover {
        background-color: $primary25;
        border-color: $primary75;
        color: $primary75;
      }
      &:active {
        background-color: $primary50;
        border-color: $primary100;
        color: $primary100;
      }
    } @else if $type == secondary {
      border-color: $secondary50;
      color: $secondary50;
      &:hover {
        border-color: $secondary75;
        color: $secondary75;
      }
      &:active {
        border-color: $secondary100;
        color: $secondary100;
      }
    }
  } @else if $variant == btnText {
    background-color: $transparent;
    border: none;
    color: $default50;

    &:hover {
      background-color: $default25;
      color: $default75;
    }

    &:active {
      background-color: $default50;
      color: $default100;
    }

    @if $type == primary {
      color: $primary50;
      &:hover {
        background-color: $primary25;
        color: $primary75;
      }
      &:active {
        background-color: $primary50;
        color: $primary100;
      }
    } @else if $type == secondary {
      color: $secondary50;
      &:hover {
        background-color: $secondary25;
        color: $secondary75;
      }
      &:active {
        background-color: $secondary50;
        color: $secondary100;
      }
    }
  } @else if $variant == btnFilled {
    background-color: $default50;
    border: none;
    color: $default100;

    &:hover {

    }

    &:active {

    }
  }
}
