@import '../../../styles/const_style';
@import '../../../styles/tools';
@import '../../../styles/theme';

.Header {
  width: 100%;
  height: $headerHeight;
  background-color: $primary75;
  @include PaddingN(all, 4);
  box-shadow: 0 1px 3px $default50;
  position: relative;
  z-index: 10;
  @include flexContainer(row, nowrap, flex-start, center);
  color: $contrastWhite;
  font-weight: bold;
  font-size: 1.5rem;
}