@import '../../styles/tools';
@import '../../styles/theme';

.Modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
  @include flexContainer(column, nowrap, center, center);
  background-color: $mattGlassWhite;
  display: none;
  opacity: 0;
  transition: opacity 400ms;

  &.Modal--visible {
    display: flex;
    opacity: 1;
  }

  & > .Modal--content {
    width: 350px;
    height: 250px;
    @include flexContainer(column, nowrap, space-around, center);
    @include PaddingN(all, 4);
    background-color: $contrastWhite;
    border-radius: 4px;
    box-shadow: 2px 2px 3px $default50;

    & > .Modal--title {
      width: 100%;
      flex: 1 1 auto;
      font-size: 1.5rem;
      font-weight: bold;
      @include MarginN(top bottom, 4);
      color: $secondary75;
      text-align: center;
    }

    & > .Modal--text {
      width: 100%;
      flex: 1 1 auto;
      text-indent: 1rem;
      line-height: 150%;
      text-align: justify;
    }

    & > .Modal--actions {
      width: 100%;
      flex: 1 1 auto;
      @include PaddingN(top, 2);
      @include flexContainer(row, nowrap, flex-end, center);

      & > .Actions--button {
        width: 75px;
        height: 24px;
        border-radius: 4px;
        border: 1px solid $default25;
        background-color: $contrastWhite;
        outline: none;
        // flex: 1 1 auto;
        @include MarginN(right, 1);

        &:last-of-type {
          @include MarginN(right, 0);
        }

        &.button-ok {
          border-color: $secondary25;
          &:hover {
            background-color: $secondary25;
          }
          &:active {
            background-color: $secondary50;
          }
        }

        &.button-cancel {
          border-color: $primary25;
          &:hover {
            background-color: $primary25;
          }
          &:active {
            background-color: $primary50;
          }
        }
      }
    }

    &.Modal--success {
      & > .Modal--title {
        color: $primary75;
      }
      & > .Modal--actions {
        & > .Actions--button {
          &.button-ok {
            border-color: $primary25;
            &:hover {
              background-color: $primary25;
            }
            &:active {
              background-color: $primary50;
            }
          }
          &.button-cancel {
            display: none;
          }
        }
      }
    }
  }
}